<template>
    <div>
        <div class="row">
            <div class="col-lg-12">
                <bt-portlet portlet-title="教室面板" :portlet-tools="true" :portlet-active="true">
                    <div class="form-horizontal">
                        <div class="form-body pal">
                            <div class="form-group">
                                <label for="inputClass" class="col-md-3 control-label">教室名称
                                </label>
                                <div class="col-md-9">
                                    <div class="input-icon"><i class="fa fa-user"></i>
                                        <input id="inputClass" type="text" placeholder="教室名称" class="form-control" v-model.lazy="inputClass" @change="handleChange($event.target.value)">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="form-actions none-bg">
                            <div class="col-md-offset-3 col-md-9">
                                <button class="btn btn-primary" @click="handleShowModals"> <i class="fa fa-upload mrs"></i>添加</button>
                                <button class="btn btn-primary" @click="handleClear"> <i class="fa fa-trash-o mrs"></i>搜索重置</button>
                            </div>
                        </div>
                    </div>
                </bt-portlet>
            </div>
        </div>
        <el-table :data="tableData" row-key="_id" class="table" stripe border>
            <el-table-column type="index" width="50"></el-table-column>
            <template v-for="item in fieldColumn">
                <el-table-column :prop="item.name" :label="item.desc"></el-table-column>
            </template>
            <el-table-column label="操作" width="160">
                <template scope="scope">
                    <el-button size="small" @click="editShowModals(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <bt-modals :modals-active="showModals" :modalsTitle="modalsTitle" @close="handleClose" @save="handleSave">
            <bt-form :item-data="modalsdata" ref="modalform" :form-data="formData" />
        </bt-modals>
        <div class="panel panel-default">
            <div class="row wrapper no-gutter">
                <div class="row wrapper no-gutter">
                    <div class="col-xs-12 col-md-4 m-t">
                        <div class="padder">
                            <div class="input-group w-full">
                                <div class="input-group">
                                    <div class="input-group-btn" ng-init="
                  filter.fields = [
                    {name:'class_name',value:'班级名'},
                    {name:'master',value:'老师姓名'}
                  ];
                  grid.search_key = 'class_name';
                  grid.search_value = '';
                  ">
                                        <button type="button" class="btn btn-default btn-sm ng-pristine ng-untouched ng-valid" ng-model="grid.search_key" data-html="1" bs-options="item.name as item.value for item in filter.fields" bs-select="">班级名&nbsp;<span class="caret"></span></button>
                                    </div>
                                    <input type="text" class="input-sm form-control ng-pristine ng-untouched ng-valid" ng-model="grid.search_value" placeholder="关键字"> <span class="input-group-btn"><button class="btn btn-sm btn-default" type="button" ng-click="grid.params._field=grid.search_key;grid.params.__field=grid.search_value">搜索</button></span></div>
                            </div>
                        </div>
                    </div>
                    <div class="col-xs-12 col-md-8 m-t">
                        <button class="btn btn-default btn-sm" ng-click="open_aside_left('search',{backdrop:false});"><i class="fa fa-filter"></i>过滤</button>
                        <div class="btn-group m-l">
                            <label btn-radio="'1'" ng-model="params.status" class="btn btn-default ng-pristine ng-untouched ng-valid">已开课</label>
                            <label btn-radio="'0'" ng-model="params.status" class="btn btn-default ng-pristine ng-untouched ng-valid">未开课</label>
                            <label btn-radio="'2'" ng-model="params.status" class="btn btn-default ng-pristine ng-untouched ng-valid">已结课</label>
                            <label btn-radio="" ng-model="params.status" class="btn btn-default ng-pristine ng-untouched ng-valid active">所有</label>
                        </div>
                        <!-- ngIf: teacher_rest -->
                        <div class="inline w-md m-l-xs ng-scope" ng-if="teacher_rest">
                            <select ng-model="params.master_oe_id" class="form-control input-sm ng-pristine ng-untouched ng-valid" ui-jq="chosen" name="oe_id" ng-options="item.oe_id as item.name for item in teacher_rest.$list" style="display: none;">
                                <option value="" class="">选择老师</option>
                                <option value="0">陈佳木</option>
                            </select>
                            <div class="chosen-container chosen-container-single" style="width: 240px;" title=""><a class="chosen-single" tabindex="-1"><span>选择老师</span><div><b></b></div></a>
                                <div class="chosen-drop">
                                    <div class="chosen-search">
                                        <input type="text" autocomplete="off">
                                    </div>
                                    <ul class="chosen-results"></ul>
                                </div>
                            </div>
                        </div>
                        <!-- end ngIf: teacher_rest -->
                        <div class="btn-group m-l-xs" ng-hide="app.isWeiXin">
                            <label btn-radio="'image'" ng-model="view_mode" class="btn btn-default ng-pristine ng-untouched ng-valid"><i class="fa fa-image"></i> 区块</label>
                            <label btn-radio="'list'" ng-model="view_mode" class="btn btn-default ng-pristine ng-untouched ng-valid active"><i class="fa fa-list"></i> 列表</label>
                        </div>
                        <!-- ngIf: selected_count()>0 --><a class="btn btn-success m-l-xs ng-hide" ng-click="bat_class_dispatch()" ng-hide="no_class_selected()">批量调课</a> <a ui-per="class.add" class="btn btn-primary pull-right" ng-click="$util.open('tpl/app/lesson/classes/open_class.modal.html','md')"><i class="fa fa-plus"></i>开班</a></div>
                </div>
                <div class="table-responsive ng-scope" ng-if="view_mode == 'list'">
                    <table class="table table-striped table-hover vline">
                        <thead>
                            <tr>
                                <th>操作</th>
                                <th>班级名 <span class="table-header-sort ng-isolate-scope" header-sort="class_name" params="params"><span class="icon"><i class="fa fa-sort-asc" ng-class="{'active':is_sort('ASC')}" ng-click="asc()"></i></span><span class="icon"><i class="fa fa-sort-desc" ng-class="{'active':is_sort('DESC')}" ng-click="desc()"></i></span></span>
                                </th>
                                <th>老师</th>
                                <th>课程名</th>
                                <th>开课日期</th>
                                <th>最后考勤</th>
                                <th>所在校区</th>
                                <th>招生情况</th>
                                <th>排课情况</th>
                                <th>考勤情况</th>
                                <th>缴费情况</th>
                            </tr>
                        </thead>
                        <tbody>
                            <!-- ngRepeat: item in grid.data -->
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>
<style>
.table {
    margin-top: 0px;
}
</style>
<script>
import langConfig from '~/lang'
import classroomStore from '~/store/pages/classroom.js'
import * as types from '~/store/mutation-types'
import lodash from 'lodash'

export default {
    name: 'BtModuleTable',
    data() {
        return {
            langConfig,
            showModals: false,
            modalsdata: classroomStore.classroom,
            tableName: ['classroom'],
            panelStyle: ['orange', 'violet', 'green', 'blue', 'yellow', 'pink'],
            modalsType: types.APPEND_API,
            formData: {},
            inputClass: '',
            classActive: '',
            tableData: [],

        }
    },
    computed: {
        menuShow() {
            if (this.menuOpen) {
                this.menuOpen = this.menuFocus || this.menuEnter
            } else {
                this.menuOpen = this.menuFocus && this.menuEnter
            }
            return this.menuOpen
        },
        modalsTitle() {
            let title = '添加教室'
            if (this.modalsType == types.APPEND_API) {
                title = '添加教室'
            } else {
                title = '编辑教室'
            }
            return title
        },
        fieldColumn() {
            var tablefield = this.modalsdata
            var fields = []
            for (var item in tablefield) {
                if (tablefield[item].fieldColumn) {
                    fields.push(tablefield[item])
                }
            }
            return fields
        },
    },
    beforeMount() {
        this.operationGet()
    },
    methods: {
        btnStyleName(index) {
            var styleName = 'btn-orange'
            if (this.panelStyle) {
                styleName = 'btn-' + this.getPanelStyle(index)
            }
            return styleName
        },
        getPanelStyle(index) {
            let indexs = index % 6
            let value = this.panelStyle[indexs]
            return value
        },
        eventData() {
            let vm = this

            vm.$store.commit(types.GET_CURRENT_API, 'classroom')

            let filterData = []
            if (vm.$store.getters.getCurrentModel['classroom']) {
                let data = vm.$store.getters.getCurrentModel['classroom'].data

                filterData = lodash.filter(data, o => {
                    let result = false
                    let className = this.inputClass.trim()

                    if (className.length > 0) {
                        result = (o.name.indexOf(className) != -1)
                    } else {
                        result = true
                    }
                    return result
                })

                this.tableData = filterData
            }
            return filterData
        },
        operationGet() {
            let vm = this
            vm.$store.dispatch(types.GET_ARRAY_API, vm.tableName).then(() => {
                this.eventData()
            })
        },
        handleClear() {
            this.inputClass = ''
            this.eventData()
        },
        handleChange() {
            this.eventData()
        },
        handleDelete(index, row) {
            let vm = this
            this.$confirm('教室删除操作, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                vm.$store.dispatch(types.DELETE_API, {
                    'model': 'classroom',
                    'id': row._id,
                }).then(() => {
                    this.operationGet()
                    this.$notify({
                        title: '成功',
                        message: '教室删除成功!',
                        type: 'success'
                    })
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                })
            })
        },
        handleClose() {
            this.showModals = false
        },
        handleSave() {
            let vm = this
            let modalform = vm.$refs.modalform
            let modalformValue = modalform.getForm()
            let id = modalform.getValue('_id')
            if (modalformValue.validate) {
                if (this.modalsType == types.APPEND_API) {
                    vm.$store.dispatch(types.APPEND_API, {
                        'model': 'classroom',
                        'form': modalformValue.form
                    }).then(() => {
                        this.$notify({
                            title: '成功',
                            message: '教室新建成功',
                            type: 'success'
                        })
                        this.inputClass = ''
                        this.operationGet()
                        vm.showModals = false
                    })
                } else {
                    vm.$store.dispatch(types.EDIT_API, {
                        'model': 'classroom',
                        'id': id,
                        'form': modalformValue.form,
                    }).then(() => {
                        this.$notify({
                            title: '成功',
                            message: '教室修改成功!',
                            type: 'success'
                        })
                        this.operationGet()
                        vm.showModals = false
                    })
                }
            }
        },
        handleShowModals() {
            this.formData = {}
            this.modalsType = types.APPEND_API
            this.showModals = true
            return false
        },
        editShowModals(index, row) {
            let vm = this
            vm.$store.dispatch(types.GET_ID_API, {
                'model': 'classroom',
                'id': row._id
            }).then((data) => {
                this.formData = data.data
                this.modalsType = types.EDIT_API
                this.showModals = true
            })
            return false
        }
    }
}
</script>



